  position: fixed;
  transition: all 0.1s ease-out;
  top: 0;
  padding-top: calc(70px + 20px);
  box-shadow: 0 0 35px 0 rgba(154, 161, 171, 0.15);
  z-index: 1;
}

.logo-box {
  height: 70px;
