.checkbox label {
  padding-right: 8px;
  padding-left: 0;
}
.checkbox label::before {
  left: auto;
  right: 0;
  margin-left: 0;
  margin-right: -18px;
}
.checkbox label::after {
  left: auto;
  right: 0;
  margin-right: -18px;
  margin-left: 0;
  padding-left: 0;
  padding-right: 3px;
}
.checkbox input[type=checkbox]:checked + label::after {
  left: auto;
  right: 7px;
  transform: rotate(45deg);
}

.checkbox.checkbox-single label:before {
  margin-right: 0;
}
.checkbox.checkbox-single label:after {
  margin-right: 0;
}

